import { Action } from "@ngrx/store";

export const TOGGLE_RSV = "[READER] Toggle RSV";
export const TOGGLE_PLAY = "[READER] Play";
export const WPM = "[READER] WPM";
export const PHRASE_LENGTH = "[READER] Phrase Length";
export const BOOK_POSITION = "[READER] Book Position";

export class ToggleRsvAction implements Action {
	readonly type = TOGGLE_RSV;
}

export class TogglePlayAction implements Action {
	readonly type = TOGGLE_PLAY;
}

export class WpmAction implements Action {
	readonly type = WPM;
	constructor(public payload: number) { }
}

export class PhraseLengthAction implements Action {
	readonly type = PHRASE_LENGTH;
	constructor(public payload: number) { }
}

export type Actions =
	PhraseLengthAction |
	TogglePlayAction |
	ToggleRsvAction |
	WpmAction;



