import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { RouterModule } from "@angular/router";

import { ComponentsModule } from "../components";

import { BookDetailPageComponent } from "./book-detail-page";
import { BookListPageComponent } from "./book-list-page";
import { FullTextViewComponent } from "./full-text-view";
import { LoginPageComponent } from "./login-page";
import { NewBookPageComponent } from "./new-book-page";
import { RsvViewComponent } from "./rsv-view";

const DIRECTIVES = [
	BookDetailPageComponent,
	BookListPageComponent,
	FullTextViewComponent,
	LoginPageComponent,
	NewBookPageComponent,
	RsvViewComponent
];

@NgModule({
	imports: [
		CommonModule,
		ComponentsModule,
		FormsModule,
		RouterModule
	],
	declarations: DIRECTIVES,
	exports: DIRECTIVES
})
export class ContainersModule { }



