import { AfterViewInit, Component, ElementRef, EventEmitter,
	Input, Output, ViewChild } from "@angular/core";

import { Observable } from "rxjs";

@Component({
	selector: "app-rsv-controls",
	templateUrl: "./rsv-controls.template.html"
})
export class RsvControlsComponent implements AfterViewInit {
	@ViewChild("wpmInput") wpmInput: ElementRef;

	@Input()
	play: boolean;

	@Input()
	wpm: number;

	@Output()
	toggleRsv: EventEmitter<void> = new EventEmitter<void>();

	@Output()
	togglePlay: EventEmitter<void> = new EventEmitter<void>();

	@Output()
	wpmChange: EventEmitter<number> = new EventEmitter<number>();

	ngAfterViewInit(): void {
		const nativeInput: HTMLInputElement = this.wpmInput.nativeElement as HTMLInputElement;
		Observable.fromEvent(nativeInput, "input")
			.debounceTime(300)
			.subscribe(() => {
				this.wpmChange.emit(Number(nativeInput.value));
			});
	}
}



