export interface ParagraphJson {
	id?: number;
	bookId?: number;
	firstIndex: number;
	lastIndex: number;
	text: string[];
}

export class Paragraph implements ParagraphJson {
	id?: number;
	firstIndex: number;
	lastIndex: number;
	text: string[];

	get length(): number {
		return this.text.length;
	}

	get textString(): string {
		return this.text.join(" ");
	}

	constructor(text: string, firstIndex: number, id?: number) {
		this.firstIndex = firstIndex;
		this.text = text
			.split(/\s{1,}/)
			.filter((str: string) => str != null && str !== "" && str.length > 0 && !str.match(/^\s*$/));
		this.lastIndex = this.firstIndex + this.text.length - 1;
		this.id = id != null ? id : undefined;
	}
}



