import { NgModule } from "@angular/core";
import { HttpModule } from "@angular/http";

import { RestHttp } from "./base/rest-http.service";

import { AuthApiModule } from "./auth";
import { SpecializedApiModule } from "./specialized";

@NgModule({
	providers: [RestHttp],
	exports: [
		AuthApiModule,
		HttpModule,
		SpecializedApiModule
	]
})
export class ApiModule { }



