import { Observable } from "rxjs";

import { AuthService } from "../../auth";
import { AuthTokenService } from "../../browser-utils";

import { ReadableApi } from "./readable-api.service";
import { RestHttp } from "./rest-http.service";

export abstract class WritableApi<M extends {id?: string | number}> extends ReadableApi<M> {
	constructor(
		protected rest: RestHttp,
		authService: AuthService,
		tokenService: AuthTokenService
	) {
		super(rest, authService, tokenService);
	}

	create(model: M): Observable<M> {
		return this.rest.post(this.modelPath, model, undefined, this.options).map((res: M) => {
			return res;
		});
	}

	update(model: M): Observable<M> {
		if (model.id == null) {
			throw new Error(`Missing id on model, cannot make an update request`);
		}

		return this.rest.put([this.modelPath, model.id], model, undefined, this.options).map((res: M) => {
			return res;
		});
	}
}



