import { ChangeDetectionStrategy, Component, EventEmitter, Inject, Input, NgZone } from "@angular/core";

import { DOCUMENT } from "@angular/platform-browser";

import { Observable } from "rxjs";
import { Store } from "@ngrx/store";

import * as fromRoot from "../../reducers";
import { Book, BookPosition, Paragraph } from "../../models";

import { BookPositionService } from "../../services";

@Component({
	selector: "app-full-text-view",
	templateUrl: "./full-text-view.template.html",
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class FullTextViewComponent {

	paragraphs: Paragraph[];


	inputsChange: EventEmitter<void> = new EventEmitter<void>();



	@Input()
	set book(val: Book) {
		if (val === this._book) {
			return;
		}
		this._book = val;
		this.inputsChange.emit();
	}
	get book(): Book {
		return this._book;
	}
	private _book: Book;



	@Input()
	set bookPosition(val: BookPosition) {
		if (val === this._bookPosition) {
			return;
		}
		this._bookPosition = val;
		this.inputsChange.emit();
	}
	get bookPosition(): BookPosition {
		return this._bookPosition;
	}
	_bookPosition: BookPosition;



	private document: Document;



	constructor(
		private bookPositionService: BookPositionService,
		private ngZone: NgZone,
		@Inject(DOCUMENT) document: any
	) {
		this.document = document;
		this.initInputsSubscription();
	}

	private initInputsSubscription(): void {
		this.inputsChange.subscribe(() => {
			if (this.book == null || this.bookPosition == null) {
				return;
			}
			this.paragraphs = this.bookPositionService.getPartialText(this.book, this.bookPosition);
			this.ngZone.runOutsideAngular(() => setTimeout(() => this.scrollToBookPosition()));
			;
		});
	}

	private scrollToBookPosition(): void {
		const targetEl: HTMLElement = this.document.getElementById(`paragraph--${ this.bookPosition.paragraphIdx }`);

		if (targetEl == null) {
			return;
		}

		const offset: number = targetEl.offsetTop;
		this.document.body.scrollTop = offset - this.document.defaultView.innerHeight / 5; // don't want it all the way scrolled up
	}
}



