import { Inject, Injectable } from "@angular/core";

import { Store } from "@ngrx/store";

import { Observable } from "rxjs";

import * as fromRoot from "../../reducers";
import * as userDetails from "../../actions/user-details";

import { User } from "../../models";

import { AuthTokenApi, AuthToken } from "../api/auth";
import { LocalStorageService } from "../browser-utils";

import { AUTH_TOKEN_STORAGE_KEY, AuthTokenService } from "../browser-utils";

@Injectable()
export class AuthService {
	initted: boolean = false;

	constructor(
		private api: AuthTokenApi,
		private tokenService: AuthTokenService,
		private store: Store<fromRoot.State>
	) { }

	verify(): Observable<User> {
		return this.api
			.verify()
			.do((details: User) => {
				this.store.dispatch(new userDetails.SetAction(details));
				this.initted = true;
			});
	}

	login(email: string, password: string): Observable<boolean> {
		return this.api
			.get(email, password)
			.map((token: AuthToken) => {
				if (token == null || token.jwt == null) {
					this.logout();
					return false;
				}

				this.tokenService.set(token.jwt);
				this.store.dispatch(new userDetails.LoginAction());
				return true;
			});
	}

	logout(): void {
		this.tokenService.delete();
		this.store.dispatch(new userDetails.LogoutAction());
	}
}



