import { Action } from "@ngrx/store";

import { Book, JsonBook } from "../models";

export const GET_ALL = "[BOOK] Get All";
export const GET_BY_ID = "[BOOK] Get By Id";
export const LOAD = "[BOOK] Load";
export const WRITE = "[BOOK] Write";

export class GetAllAction implements Action {
	readonly type = GET_ALL;
}

export class GetByIdAction implements Action {
	readonly type = GET_BY_ID;
	constructor(public payload: number) { }
}

export class LoadAction implements Action {
	readonly type = LOAD;
	constructor(public payload: Book) { }
}

export class WriteAction implements Action {
	readonly type = WRITE;
	constructor(public payload: JsonBook) { }
}
export class WriteFinishAction implements Action {
	readonly type = WRITE;
}

export type Actions =
	GetAllAction |
	GetByIdAction |
	LoadAction |
	WriteFinishAction |
	WriteAction;



