import { Action } from "@ngrx/store";

import { BookPosition } from "../models";

export const LOAD_SAVED = "[BOOK_POSITION] Load Saved";
export const SET = "[BOOK_POSITION] Set";
export const SET_PARTIAL_TEXT = "[BOOK_POSITION] Set Partial Text";
export const STEP = "[BOOK_POSITION] Step";

export class LoadSavedAction implements Action {
	readonly type = LOAD_SAVED;
	constructor(public payload: number) { }
}

export class SetAction implements Action {
	readonly type = SET;
	constructor(public payload: BookPosition) { }
}

export class SetPartialTextAction implements Action {
	readonly type = SET_PARTIAL_TEXT;
}

export class StepAction implements Action {
	readonly type = STEP;
	constructor(public payload: number) { }
}

export type Actions =
	SetAction |
	StepAction |
	LoadSavedAction;



