import { Component, HostListener } from "@angular/core";

import { JsonBook } from "../../models";

import { BooksApi } from "../../services/api";

@Component({
	templateUrl: "./new-book-page.template.html"
})
export class NewBookPageComponent {
	book: JsonBook = {
		title: "",
		author: "",
		body: ""
	};

	@HostListener("document:keydown", ["$event"])
	handleEnter(event: KeyboardEvent): void {
		if (event.key !== "Enter") {
			return;
		}
		event.preventDefault();
		this.submit();
	}


	constructor(private api: BooksApi) {}

	submit(): void {
		this.api.createFromJsonBook(this.book)
			.subscribe((success) => {
					if (success) {
						return;
					}
					console.log("login failed");
				},
				() => {
					console.log("login failed");
				}
			);
	}
}



