import { createSelector } from "reselect";
import { ActionReducer, combineReducers } from "@ngrx/store";
import * as fromRouter from "@ngrx/router-store";
import { environment } from "../../environments/environment";

import { compose } from "@ngrx/core/compose";
import { storeFreeze } from "ngrx-store-freeze";

import * as fromReader from "./reader";
import * as fromBookPosition from "./book-position";
import * as fromBooks from "./books";
import * as fromUserDetails from "./user-details";

export interface State {
	reader: fromReader.State;
	books: fromBooks.State;
	bookPosition: fromBookPosition.State;
	userDetails: fromUserDetails.State;
}

const reducers = {
	reader: fromReader.reducer,
	books: fromBooks.reducer,
	bookPosition: fromBookPosition.reducer,
	userDetails: fromUserDetails.reducer
};

const devReducer: ActionReducer<State> = compose(storeFreeze, combineReducers)(reducers);
const prodReducer: ActionReducer<State> = combineReducers(reducers);

export function reducer(state: any, action: any) {
	if (environment.production) {
		return prodReducer(state, action);
	} else {
		return devReducer(state, action);
	}
}

// READER STATE REDUCERS
export const getReaderState = (state: State) => state.reader;
export const getPlay = createSelector(getReaderState, fromReader.getPlay);
export const getRsv = createSelector(getReaderState, fromReader.getRsv);
export const getWpm = createSelector(getReaderState, fromReader.getWpm);
export const getPhraseLength = createSelector(getReaderState, fromReader.getPhraseLength);

// BOOK STATE REDUCERS
export const getBooksState = (state: State) => state.books;
export const getBook = createSelector(getBooksState, fromBooks.getBook);

// BOOK POSITION STATE REDUCERS
export const getBookPositionState = (state: State) => state.bookPosition;
export const getBookPosition = createSelector(getBookPositionState, fromBookPosition.getPosition);

// USER REDUCERS
export const getUserState = (state: State) => state.userDetails;
export const getLoggedIn = createSelector(getUserState, fromUserDetails.getLoggedIn);
export const getUserId = createSelector(getUserState, fromUserDetails.getUserId);
export const getUserEmail = createSelector(getUserState, fromUserDetails.getUserEmail);
export const getUserName = createSelector(getUserState, fromUserDetails.getUserName);



