import { Component, OnDestroy, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";

import * as fromRoot from "../../reducers";
import { Store } from "@ngrx/store";
import { Observable, Subscription } from "rxjs";

import * as reader from "../../actions/reader";
import * as books from "../../actions/books";
import * as bookPosition from "../../actions/book-position";

import { Book, BookPosition } from "../../models";
import { BooksApi, BookPositionService, IntervalService } from "../../services";

@Component({
	selector: "app-book-detail",
	templateUrl: "./book-detail-page.template.html"
})
export class BookDetailPageComponent implements OnDestroy, OnInit {
	currentPhrase: string;

	// STORE ITEMS
	book$: Observable<Book>;
	bookPosition$: Observable<BookPosition>;
	rsvEnabled$: Observable<boolean>;
	play$: Observable<boolean>;
	phraseLength$: Observable<number>;
	wpm$: Observable<number>;

	private routeParamsSubscription: Subscription;

	constructor(
		private route: ActivatedRoute,
		private booksApi: BooksApi,
		private bookPositionService: BookPositionService,
		private store: Store<fromRoot.State>
	) {
		this.rsvEnabled$ = this.store.select(fromRoot.getRsv);
		this.phraseLength$ = this.store.select(fromRoot.getPhraseLength);
		this.book$ = this.store.select(fromRoot.getBook);
		this.bookPosition$ = this.store.select(fromRoot.getBookPosition);
		this.play$ = this.store.select(fromRoot.getPlay);
		this.wpm$ = this.store.select(fromRoot.getWpm);
	}

	ngOnInit(): void {
		this.book$
			.filter((book) => book != null)
			.take(1)
			.subscribe((book: Book) => {
				this.store.dispatch(new bookPosition.LoadSavedAction(book.id));
			});

		this.getBookById();
		this.initBookPositionSubscription();
	}

	ngOnDestroy(): void { }

	toggleRsv(): void {
		this.store.dispatch(new reader.ToggleRsvAction());
	}

	togglePlay(): void {
		this.store.dispatch(new reader.TogglePlayAction());
	}

	setWpm(wpm: number): void {
		this.store.dispatch(new reader.WpmAction(wpm));
	}

	private getBookById(): void {
		const bookId: number = this.route.snapshot.params.id;
		this.store.dispatch(new books.GetByIdAction(bookId));
	}

	private initBookPositionSubscription(): Subscription {
		return this.bookPosition$
			.withLatestFrom(this.book$, this.phraseLength$)
			.subscribe(([bookPosition, book, phraseLength]) => {
				if (bookPosition == null || book == null) {
					return;
				}
				this.currentPhrase = this.bookPositionService.getCurrentPhrase(book, bookPosition, phraseLength);
			});
	}
}



