import { Injectable } from "@angular/core";
import { Effect, Actions, toPayload } from "@ngrx/effects";
import { Action, Store } from "@ngrx/store";

import { Observable } from "rxjs";

import * as fromRoot from "../reducers";

import { Book, Bookmark, BookPosition } from "../models";
import { BookPositionService } from "../services/utils";
import { BookmarksApi } from "../services/api";

import * as bookPosition from "../actions/book-position";

@Injectable()
export class BookPositionEffects {
	// STORE ITEMS
	book$: Observable<Book>;
	bookPosition$: Observable<BookPosition>;

	@Effect()
	save: Observable<Bookmark>;

	@Effect()
	step: Observable<Action>;

	@Effect()
	loadSavedPosition: Observable<Action> = this.actions$
		.ofType(bookPosition.LOAD_SAVED)
		.map(toPayload)
		.switchMap((id) => {
			if (id == null) {
				return Observable.empty();
			}
			const nextGetById$ = this.actions$.ofType(bookPosition.LOAD_SAVED).skip(1);
			return this.bookmarksApi
				.getById(id)
				.takeUntil(nextGetById$)
				.map((bookmark: Bookmark) => {
					return new bookPosition.SetAction({
						paragraphIdx: bookmark.paragraphIdx,
						wordIdx: bookmark.wordIdx
					});
				});
		});

	constructor(
		private actions$: Actions,
		private bookmarksApi: BookmarksApi,
		private bookPositionService: BookPositionService,
		private store: Store<fromRoot.State>
	) {
		this.book$ = this.store.select(fromRoot.getBook);
		this.bookPosition$ = this.store.select(fromRoot.getBookPosition);
		this.step = this.getStepEffect();
		this.save = this.getSaveEffect();
	}

	getSaveEffect(): Observable<Bookmark> {
		return this.actions$
			.ofType(bookPosition.SET)
			.map(toPayload)
			.distinct((bookPosition) => bookPosition.paragraphIdx)
			.withLatestFrom(this.book$)
			.flatMap(([bookPosition, book]) => {
				if (book == null || bookPosition.paragraphIdx == null || bookPosition.wordIdx == null) {
					return Observable.empty();
				}

				return this.bookmarksApi.create(new Bookmark(bookPosition.paragraphIdx, 0, book.id));
			});
	}

	getStepEffect(): Observable<Action> {
		return this.actions$
			.ofType(bookPosition.STEP)
			.map(toPayload)
			.withLatestFrom(this.book$, this.bookPosition$)
			.flatMap(([step, book, currentPosition]) => {
				if (step == null || currentPosition == null || book == null) {
					return Observable.empty();
				}
				const nextPosition = this.bookPositionService.stepPosition(book, currentPosition, step);
				return Observable.of(new bookPosition.SetAction(nextPosition));
			});
	}
}



