import { Injectable } from "@angular/core";
import { Http, RequestOptions, RequestOptionsArgs, Response, URLSearchParams } from "@angular/http";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/map";

import { RestHttpConfig } from "./rest-http.config";

export type QueryValue = number | string | boolean;
export interface Query {
	[key: string]: QueryValue | QueryValue[] | undefined;
}

export type RouteValue = number | string;
export type Route = RouteValue | RouteValue[];

@Injectable()
export class RestHttp {
	constructor(
		protected config: RestHttpConfig,
		protected http: Http) {

	}

	get baseUrl(): string {
		return this.config.baseUrl;
	}

	request<R>(route: Route, options?: RequestOptionsArgs): Observable<R> {
		return this.wrapResponse<R>(
			this.http.request(this.getPath(route), options)
		);
	}

	get<R>(route: Route, query?: Query, options?: RequestOptionsArgs): Observable<R> {
		return this.wrapResponse<R>(
			this.http.get(this.getPath(route), addQueryToOptions(options, query))
		);
	}

	post<T, R>(route: Route, body: T, query?: Query, options?: RequestOptionsArgs): Observable<R> {
		return this.wrapResponse<R>(
			this.http.post(this.getPath(route), body, addQueryToOptions(options, query))
		);
	}

	put<T, R>(route: Route, body: T, query?: Query, options?: RequestOptionsArgs): Observable<R> {
		return this.wrapResponse<R>(
			this.http.put(this.getPath(route), body, addQueryToOptions(options, query))
		);
	}

	patch<T, R>(route: Route, body: T, query?: Query, options?: RequestOptionsArgs): Observable<R> {
		return this.wrapResponse<R>(
			this.http.patch(this.getPath(route), body, addQueryToOptions(options, query))
		);
	}

	delete<R>(route: Route, query?: Query, options?: RequestOptionsArgs): Observable<R> {
		return this.wrapResponse<R>(
			this.http.delete(this.getPath(route), addQueryToOptions(options, query))
		);
	}

	protected wrapResponse<R>(obs: Observable<Response>): Observable<R> {
		return obs.map((res: Response) => {
			try {
				return res.json() as R;
			} catch (e) {
				return res.text();
			}
		});
	}

	protected getPath(route: Route): string {
		return getPath(this.baseUrl, route);
	}
}

const ROUTE_TRIM_REGEXP = /^\/*(.*?)\/*$/;
export function getPath(baseUrl: string, route: Route): string {
	if (Array.isArray(route)) {
		route = route.join("/");
	} else {
		route = String(route);
	}

	route = route.replace(ROUTE_TRIM_REGEXP, "$1");
	return baseUrl + "/" + route;
}

export function addQueryToOptions(options?: RequestOptionsArgs, query?: Query): RequestOptionsArgs | undefined {
	if (query == null) {
		return options;
	}
	if (options == null) {
		options = {};
	}
	options.search = queryToSearchParams(query);
	return options;
}

export function queryToSearchParams(query: Query): URLSearchParams {
	return Object.keys(query)
		.reduce((search, key) => {
			const value: QueryValue | QueryValue[] | undefined = query[key];
			if (Array.isArray(value)) {
				value.forEach((val) => search.append(key, String(val)));
			} else if (value != null) {
				search.set(key, String(value));
			}
			return search;
		}, new URLSearchParams());
}



