import { Injectable } from "@angular/core";

import { Observable } from "rxjs";

import { Book, JsonBook, Paragraph } from "../../../models";

import { AuthService } from "../../auth";
import { AuthTokenService } from "../../browser-utils";

import { WritableApi } from "../base/writable-api.service";
import { RestHttp } from "../base/rest-http.service";

import { ParagraphParserService } from "../../utils";

export interface JsonBook  {
	id: number;
	title: string;
	author: string;
	body: string;
}

@Injectable()
export class BooksApi extends WritableApi<Book> {
	modelPath: string = "books";

	constructor(
		private paragraphParserService: ParagraphParserService,
		rest: RestHttp,
		authService: AuthService,
		tokenService: AuthTokenService
	) {
		super(rest, authService, tokenService);
	}

	getById(id: number): Observable<Book> {
		return super.getById(id)
			.map((res) => {
				const content: Paragraph[] = this.paragraphParserService.parse(res["body"]);
				const length: number = content.reduce((sum: number, paragraph: Paragraph) => {
						sum += paragraph.length;
						return sum;
					}, 0);
				return {
					id: res.id,
					title: res.title,
					author: res.author,
					content,
					length
				};
			});
	}

	createFromJsonBook(json: JsonBook): Observable<JsonBook> {
		return this.rest
			.post(this.modelPath, json, undefined, this.options)
			.map((res: JsonBook) => {
				return res;
			});
	}

	fromJson(res: any): Book {
		const content: Paragraph[] = this.paragraphParserService.parse(res["body"]);
		const length: number = content.reduce((sum: number, paragraph: Paragraph) => {
				sum += paragraph.length;
				return sum;
			}, 0);
		return {
			id: res.id,
			title: res.title,
			author: res.author,
			content,
			length
		};
	}
}



