import { Injectable } from "@angular/core";
import { Effect, Actions, toPayload } from "@ngrx/effects";
import { Action } from "@ngrx/store";

import { Observable } from "rxjs";

import { Book } from "../models";
import { BooksApi } from "../services";

import * as books from "../actions/books";

@Injectable()
export class BookEffects {
	@Effect()
	getById$: Observable<Action> = this.actions$
		.ofType(books.GET_BY_ID)
		.map(toPayload)
		.switchMap((id) => {
			if (id == null) {
				return Observable.empty();
			}

			const nextGetById$ = this.actions$.ofType(books.GET_BY_ID).skip(1);
			return this.booksApi
				.getById(id)
				.takeUntil(nextGetById$)
				.map((book: Book) => new books.LoadAction(book));
		});

	@Effect()
	write$: Observable<Action> = this.actions$
		.ofType(books.WRITE)
		.map(toPayload)
		.switchMap((jsonBook) => {
			if (jsonBook == null) {
				return Observable.empty();
			}

			const nextWrite$ = this.actions$.ofType(books.WRITE).skip(1);
			return this.booksApi
				.createFromJsonBook(jsonBook)
				.takeUntil(nextWrite$)
				.map(() => new books.WriteFinishAction());
		});

	constructor(private actions$: Actions, private booksApi: BooksApi) {

	}
}



