import { createSelector } from "reselect";
import * as books from "../actions/books";

import { Book } from "../models";

export interface State {
	book?: Book;
}

export const initialState: State = { };

export function reducer(state: State = initialState, action: books.Actions): State {
	switch (action.type) {
		case books.LOAD:
			return Object.assign(
				{},
				state,
				{ book: action.payload }
			);
		default:
			return state;
	}
}

export const getBook = (state: State): Book => state.book;



