import { Headers, RequestOptionsArgs } from "@angular/http";

import { Observable } from "rxjs/Observable";

import { AuthService } from "../../auth";
import { AuthTokenService } from "../../browser-utils";

import { Query, RestHttp } from "./rest-http.service";
import { makeResponseEnvelope, ResponseEnvelope } from "./response-envelope";

// Good ass name huh?
export interface JsonResponse {
	body: {
		totalRecords: number,
		data: any[]
	};
}

export abstract class ReadableApi<M> {
	abstract modelPath: string;

	get options(): RequestOptionsArgs {
		return this.tokenService.getOptions();
	}

	constructor(
		protected rest: RestHttp,
		protected authService: AuthService,
		protected tokenService: AuthTokenService
	) { }

	getById(id: number): Observable<M> {
		return this.rest
			.get([this.modelPath, id], undefined, this.options)
			.map((res) => this.unwrapSingle(res));
	}

	getByFilter(query?: Query): Observable<ResponseEnvelope<M>> {
		return this.rest
			.get(this.modelPath, query, this.options)
			.map((res: any) => {
				return makeResponseEnvelope(
					res.data,
					res.totalRecords
				);
			});
	}

	protected unwrapSingle(json: any): M | undefined {
		if (json == null) {
			return;
		}
		if (Array.isArray(json.data)) {
			return json.data.length > 0 ? json.data[0] : undefined;
		}
		return json;
	}
}



