import { NgModule } from "@angular/core";

import { BrowserUtilsModule } from "../browser-utils";

import { AuthGuard } from "./auth.guard";
import { AuthService } from "./auth.service";

@NgModule({
	imports: [BrowserUtilsModule],
	providers: [
		AuthGuard,
		AuthService
	]
})
export class AuthModule { }



