import { NgModule } from "@angular/core";
import { Route, RouterModule } from "@angular/router";

import { AuthGuard } from "./services/auth";

import { BookDetailPageComponent, BookListPageComponent, NewBookPageComponent, LoginPageComponent } from "./containers";

const routes: Route[] = [
	{
		path: "books",
		children: [{
				path: "new",
				component: NewBookPageComponent,
			}, {
				path: ":id",
				component: BookDetailPageComponent
			}, {
				path: "",
				pathMatch: "full",
				component: BookListPageComponent
			}],
		canActivate: [AuthGuard],
		canActivateChild: [AuthGuard]
	},
	{
		path: "login",
		component: LoginPageComponent
	},
	{
		path: "**",
		redirectTo: "/books"
	}
];

@NgModule({
	imports: [
		RouterModule.forRoot(
			routes,
			{
				useHash: true,
				enableTracing: false
			}
		)
	],
	exports: [
		RouterModule
	]
})
export class AppRoutingModule { }



