import { createSelector } from "reselect";
import * as userDetails from "../actions/user-details";

import { User } from "../models";

export interface State extends User {
	isLoggedIn: boolean;
	id?: number;
	email?: string;
	name?: string;
}

export const initialState: User = {
	loggedIn: false
};

export function reducer(state: User = initialState, action: userDetails.Actions): User {
	switch (action.type) {
		case userDetails.SET:
			return Object.assign(
				{},
				state,
				{ ...action.payload }
			);
		default:
			return state;
	}
}

export const getLoggedIn = (state: User): boolean => state.loggedIn;
export const getUserId = (state: User): number => state.id;
export const getUserEmail = (state: User): string => state.email;
export const getUserName = (state: User): string => state.name;



