import { Injectable } from "@angular/core";

import { Observable } from "rxjs";

@Injectable()
export class IntervalService {
	getInterval(ms: number): Observable<number> {
		return Observable.interval(ms);
	}

	getIntervalFromWpm(wpm: number, wordsPerInterval: number = 1) {
		if (wpm === 0) {
			return Observable.never();
		}
		return Observable.interval(60e3 / wpm * wordsPerInterval);
	}
}



