import { Injectable } from "@angular/core";
import { Headers, Http, Response, RequestOptionsArgs } from "@angular/http";

import { Observable } from "rxjs";

import { User } from "../../../models";

import { AuthTokenService } from "../../browser-utils";

import { RestHttpConfig } from "../base/rest-http.config";

import { AuthToken } from "./auth-token";

@Injectable()
export class AuthTokenApi {
	get tokenRoute(): string {
		return this.config.baseUrl + this.tokenRouteExtension;
	}

	get usersRoute(): string {
		return this.config.baseUrl + this.usersRouteExtension;
	}

	private tokenRouteExtension: string = "/user_token";
	private usersRouteExtension: string  = "/users";

	constructor(
		private http: Http,
		protected config: RestHttpConfig,
		private tokenService: AuthTokenService
	) { }

	verify(): Observable<User> {
		return this.http
			.get(this.usersRoute, this.tokenService.getOptions())
			.map((res: Response) => res.json() as User);
	}

	get(email: string, password: string): Observable<AuthToken | undefined> {
		return this.http
			.post(this.tokenRoute, { auth: { email, password } })
			.map((res: Response) => {
				if (res == null || res.text() === "") {
					return Observable.of(undefined);
				}

				return res.json();
			});
	}
}



