import { BrowserModule } from "@angular/platform-browser";
import { LOCALE_ID, NgModule } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";

import { EffectsModule } from "@ngrx/effects";
import { StoreModule } from "@ngrx/store";

import { reducer } from "./reducers";

import { BookEffects, BookPositionEffects, UserEffects } from "./effects";

import { AppComponent } from "./app.component";
import { AppRoutingModule } from "./app-routing.module";

import { ContainersModule } from "./containers";

import { ServicesModule } from "./services";
import { provideRestHttpConfig } from "./services/api";

@NgModule({
	imports: [
		AppRoutingModule,
		BrowserModule,
		FormsModule,
		HttpModule,
		ContainersModule,
		ServicesModule,
		StoreModule.provideStore(reducer),
		EffectsModule.run(BookEffects),
		EffectsModule.run(BookPositionEffects),
		EffectsModule.run(UserEffects)
	],
	declarations: [
		AppComponent
	],
	providers: [
		provideRestHttpConfig({
			baseUrl: "http://bounds-api.herokuapp.com"
		}),
		{ provide: LOCALE_ID, useValue: "en-US"}
	],
	bootstrap: [AppComponent]
})
export class AppModule { }



