import { Injectable } from "@angular/core";

import { Paragraph } from "../../models";

@Injectable()
export class ParagraphParserService {
	parse(rawText: string): Paragraph[] {
		let firstIndex: number = 0;
		return this.splitRawParagaphs(rawText)
			.map((paragraphText: string, idx: number) => {
				const parsedParagraph = this.parseSingleParagraph(paragraphText, firstIndex, idx);
				firstIndex = firstIndex + parsedParagraph.length;
				return parsedParagraph;
			});
	}

	private splitRawParagaphs(rawText: string): string[] {
		return rawText
			.split(/\n{2,}|\r\n\r{1,}|\n\r\n{1,}/)
			.filter((str: string) => str != null && str !== "" && str.length > 0 && !str.match(/^\s*$/));
	}

	private parseSingleParagraph(paragraphText: string, firstIndex: number, idx: number): Paragraph {
		return new Paragraph(paragraphText, firstIndex, idx);
	}
}



