import { createSelector } from "reselect";
import * as bookPosition from "../actions/book-position";

import { BookPosition } from "../models";

export interface State {
	position: BookPosition;
}

export const initialState: State = {
	position: { paragraphIdx: 0, wordIdx: 0 }
};

export function reducer(state: State = initialState, action: bookPosition.Actions): State {
	switch (action.type) {
		case bookPosition.SET:
			return Object.assign(
				{},
				state,
				{ position: action.payload }
			);
		default:
			return state;
	}
}

export const getPosition = (state: State): BookPosition => state.position;



