import { createSelector } from "reselect";
import * as reader from "../actions/reader";

export interface State {
	rsv: boolean;
	phraseLength: number;
	play: boolean;
	wpm: number;
}

export const initialState: State = {
	rsv: false,
	phraseLength: 2,
	play: false,
	wpm: 350
};

export function reducer(state: State = initialState, action: reader.Actions) {
	switch (action.type) {
		case reader.TOGGLE_RSV:
			const newRsvState: boolean = !state.rsv;
			return Object.assign(
				{},
				state,
				{
					rsv: newRsvState,
					play: newRsvState ? state.play : false
				}
			);

		case reader.TOGGLE_PLAY:
			const newPlayState: boolean = !state.play;
			return Object.assign(
				{},
				state,
				{
					play: newPlayState,
					rsv: newPlayState ? true : state.rsv
				}
			);

		case reader.WPM:
			return Object.assign(
				{},
				state,
				{ wpm: action.payload }
			);

		case reader.PHRASE_LENGTH:
			return Object.assign(
				{},
				state,
				{ phraseLength: action.payload }
			);

		default:
			return state;
	}
}

export const getRsv = (state: State): boolean => state.rsv;
export const getPlay = (state: State): boolean => state.play;
export const getWpm = (state: State): number => state.wpm;
export const getPhraseLength = (state: State): number => state.phraseLength;




