import { Injectable } from "@angular/core";

import { Observable } from "rxjs";

import { Bookmark } from "../../../models";

import { makeResponseEnvelope, Query, ResponseEnvelope } from "../../api";
import { AuthService } from "../../auth";
import { AuthTokenService } from "../../browser-utils";

import { RestHttp } from "../base/rest-http.service";
import { WritableApi } from "../base/writable-api.service";

@Injectable()
export class BookmarksApi extends WritableApi<Bookmark> {
	modelPath: string = "bookmarks";

	constructor(
		rest: RestHttp,
		authService: AuthService,
		tokenService: AuthTokenService
	) {
		super(rest, authService, tokenService);
	}

	getById(bookId: number): Observable<Bookmark> {
		return super.getById(bookId).map((res) => {
			if (res == null) {
				return Observable.empty();
			}
			return new Bookmark(res.paragraphIdx, res.wordIdx, res.id);
		});
	}
}



